.accounts-profile-details-section {
    display: flex;
    align-items: flex-start;
    gap: 70px;
    background-color: white;
    border: 1px solid #E9E9E9;
    border-radius: 10px;
    padding: 24px;
}

.accounts-profile-details-section .outer-div div {
    margin-top: 8px;
    margin-bottom: 8px;

}

.accounts-profile-details-section .outer-div.custom-margin {
    margin-left: 10px;
    flex: 1;
}

.accounts-profile-details-section .outer-div.custom-margin .profile-div {
    display: flex;
    width: 100%;
}

.accounts-profile-details-section .outer-div.custom-margin .profile-div .label-text {
    max-width: 110px;
    flex: 1;
    width: 100%;
}

.accounts-profile-details-section .outer-div.round-img-div {
    max-width: 156px;
    width: 100%;
    height: 156px;
    background-color: #f5f5f5;
    border-radius: 50%;
    align-self: center;
}

.label-text {
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    color: #656565;
    margin-right: 16px;
}


@media(max-width:768.99px) {
    .accounts-profile-details-section {
        flex-direction: column;
        gap: 25px;
    }

    .accounts-profile-details-section .outer-div.round-img-div {
        align-self: flex-start;
    }

    .accounts-profile-details-section .outer-div.custom-margin {
        margin-left: 0px;
    }
}