.login-section {
    padding: 100px 40px 32px;
    background: linear-gradient(99.78deg, #0466D1 0%, #00D1A9 152.3%);
    height: 100vh;
}


.login-section .login-form-div {
    max-width: 500px;
    margin: auto auto;
    background-color: #f5f5f5;
    padding: 32px 40px;
    border-radius: 10px;
}

.login-section .logo-login {
    max-width: 220px;
    margin-bottom: 40px;
}


.login-section .login-form-div .input-outer-div .input-div .input-login {
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 22px;
    color: #212529;
    padding: 7px 17px;
    border: 1px solid #E9E9E9;
    border-radius: 8px;
    width: 100%;
    margin-bottom: 6px;
    outline: none;
}

.login-section .login-form-div .input-outer-div .input-div .remember-me-text {
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 22px;
    color: #212529;
    border-radius: 8px;
    width: 100%;
    margin-left: 4px;
}

.login-section .forgot-pass-link {
    font-size: 12px;
    margin-top: -5px;
    margin-bottom: 15px;
}

.login-section .forgot-pass-link a {
    text-decoration: none;
    color: #0466D1;
}

.login-section .login-form-div .input-outer-div .label-text {
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 18px;
    color: #021B41;
    margin: 10px 0;
}

.login-section .buttons-div {
    margin-top: 20px;
    width: 100%;
    text-align: center;
}


.login-section .buttons-div .sign-in-btn-div {
    margin-bottom: 16px;
}

.login-section .buttons-div .or-div {
    margin-bottom: 16px;
}

.login-section .buttons-div .google-btn-div {
    margin-bottom: 16px;
}

.login-section .buttons-div .sign-in-btn-div button {
    width: 100%;
    border-radius: 10px;
    outline: none;
    border: 1px solid #0466D1;
    background-color: #0466D1;
    color: white;
    padding: 5px 24px;
    font-size: 16px;
}

.login-section .buttons-div .google-btn-div button {
    width: 100%;
    border-radius: 10px;
    outline: none;
    border: 1px solid #0466D1;
    background-color: white;
    color: #212529;
    padding: 5px 24px;
    font-size: 16px;
}


.login-section .buttons-div .google-btn-div button img {
    max-width: 20px;
    margin-right: 10px;
}

.login-section .bottom-login-div {
    margin-top: 40px;
}

.login-section .bottom-login-div p {
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
    font-size: 12px;
    line-height: 14px;
}


.login-section .bottom-login-div a {
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
    font-size: 12px;
    line-height: 14px;
    text-decoration: none;
    color: #0466D1;
}

@media(max-width:575.99px) {
    .login-section .login-form-div {
        padding: 16px;
    }

    .login-section .buttons-div .google-btn-div button {
        font-size: 14px;
    }

    .login-section .buttons-div .sign-in-btn-div button {
        font-size: 14px;
    }
}