:root {
  --primary-color: #0466D1;
  --secondary-color: #00D1A9;
  --heading-dark-color: #021B41;
  --dark-text-color: #212529;
  --light-text-color: #FFFFFF;
}

* {
  margin: 0;
  padding: 0;
  text-decoration: none;
}

body {
  font-family: 'Poppins', sans-serif !important;
}
  
.primary-color {
  color: var(--primary-color);
}

.primary-background-color {
  background-color: var(--primary-color);
}

.secondary-color {
  color: var(--secondary-color);
}

.secondary-background-color {
  background-color: var(--secondary-color);
}

.light-text-color {
  color: var(--light-text-color);
}

.dark-heading-color {
  color: var(--heading-dark-color);
}

.dark-text-color {
  color: var(--dark-text-color)
}

.main-title {
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
  font-size: 64px;
  line-height: 96px;
}

.section-heading {
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
  font-size: 40px;
  line-height: 60px;
}

.heading-1 {
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
  font-size: 32px;
  line-height: 48px;
}

.heading-2 {
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
  font-size: 24px;
  line-height: 36px;
}

.subtitle-1 {
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
  font-size: 34px;
  line-height: 51px;
}

.body-text-bold {
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px;
}

.body-text-normal {
  font-family: 'Poppins', sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
}

.body-text {
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 36px;
}

.small-text {
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
}

.button-text {
  font-family: 'Poppins', sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
}

.caption-text {
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
}

.caption-text-bold {
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
  font-size: 12px;
  line-height: 14px;
}

@media(max-width:1440.99px) {
  .main-title {
    font-size: 56px;
    line-height: 72px;
  }

  .body-text {
    font-size: 18px;
    line-height: 32px;
  }
}

@media(max-width:1024.99px) {
  .body-text {
    font-size: 16px;
    line-height: 24px;
  }
}

@media(max-width:575.99px) {
  .main-title {
    font-size: 42px;
    line-height: 60px;
  }

  .section-heading {
    font-size: 32px;
    line-height: 48px;
  }
}