.dashboard-section .main-div .header .name-div .dropdown .dropdown-toggle {
    display: flex;
    align-items: center;
    outline: none;
}

.dashboard-section .main-div .header .name-div .dropdown .dropdown-toggle:focus {
    box-shadow: 0 0 0 0;
}

.dashboard-section .main-div .header .name-div .dropdown .dropdown-menu {
    padding: 0px 0px;
}


.dashboard-section .main-div .header .name-div .dropdown .dropdown-toggle .initials {
    border-radius: 50%;
    margin-right: 16px; 
    height: 50px;
    width: 50px;
    border: 2px solid transparent;
    background-image: linear-gradient(white, white), radial-gradient(circle at top left, #0466D1,#00D1A9);
    background-origin: border-box;
    background-clip: content-box, border-box;
    display: inline-block;
    overflow: hidden;
}
.dashboard-section .main-div .header .name-div .dropdown .dropdown-toggle .initials img{
   width: 100%;
   height: 100%;
   object-fit: cover;
}
Link {
    border-bottom: none;
    text-decoration: none;
}

a {
    text-decoration: none;
}
 
.dropdown-item:hover{ 
    background-image: linear-gradient(to right,#0467d183, #00d1ab96);
}

.dropdown-item li {
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
    font-size: 16px;
    line-height: 24px;
    display: flex;
    align-items: center;
    gap: 6px;
    padding: 10px 0px;
}

.dropdown-item img {
    width: 24px;
    height: 24px;
}

@media(max-width:768.99px) {

    .dashboard-section .main-div .header .name-div .dropdown .dropdown-toggle .initials {
        margin-right: 5px;
    }

    .dropdown-item li {
        font-size: 12px;
        line-height: 21px;
        padding: 6px 0px;
    }
}

@media(max-width:414.99px) {

    .dashboard-section .main-div .header .name-greetings {
        display: none;
    }
}