.navbar-section {
    padding: 24px 160px;
    margin: 0 auto;
    max-width: 1920px;
    width: 100%;
}

.navbar .navbar-nav {
    gap: 48px;
}


.navbar .navbar-nav .nav-item .nav-link {
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
    color: white;
}

.navbar-toggler-icon {
    filter: invert(1);
}

.mobile-logo {
    display: none;
}

@media(max-width:1600.99px) {
    .navbar-section {
        padding: 24px 60px;
    }
}

@media(max-width:768.99px) {
    .navbar-section {
        padding: 25px 12px;
    }
}

@media(max-width:992.99px) {
    .navbar .navbar-nav {
        gap: 20px;
    }

    .navbar-collapse {
        margin-top: 20px;
        border-top: 1px solid #ffffff3d;
    }

    .mobile-logo {
        display: block;
    }

    .desktop-logo {
        display: none;
    }

    .navbar .navbar-nav .nav-item .nav-link {
        text-align: center;
    }
}