.card {
    width: 650px;
    margin-right: 50px
}

.optionsMapWrap {
    position: relative;
    left: -5%;
}

.disambiguationWrap {
    margin-top: 40px;
    margin-left: 40px;
    height: auto;
    width: 100%;
    z-index: -1
}

.searchWrap {
    z-index: 0;
    padding: 0px;
    margin-left: 40px;
    margin-top: 40px
}

.resultsWrap {
    margin-top: 40px;
    padding: 0px;
    margin-left: 40px
}


@media screen and (max-width: 1000px) {
    .searchWrap {
        z-index: 0;
        padding: 0px;
        margin-left: 0px;
        margin-top: 40px
    }

    .disambiguationWrap {
        margin-top: 40px;
        margin-left: 5px;
        height: auto;
        width: 100%;
    }
}