.corporate-links-section {
    display: flex;
    flex-wrap: wrap;
    gap: 24px;
}

.corporate-links-section .inner-card {
    margin-top: 16px;
    flex: 1;
    border-radius: 10px;
    background-color: white;
    padding: 24px;
    min-width: 160px;
}

@media(max-width:768px) {
    .corporate-links-section .inner-card {
        min-width: 330px;
    }
}


@media(max-width:575px) {
    .corporate-links-section .inner-card {
        min-width: 250px;
    }
}