.sidebar-outer {
    display: flex;
}

main {
    width: 100%;
    background-color: #f5f5f5;
}

.sidebar {
    background: linear-gradient(180deg, #0466D1 7.81%, #00D1A9 117.33%);
    color: white;
    min-height: 100vh;
    height: 100%;
    width: 280px;
    transition: all 0.2s ease-in-out;
    padding: 24px 8px;
    position: sticky;
    top: 0;
    bottom: 0;
}


.sidebar-opened {
    width: 280px;
}

.sidebar-closed {
    width: 96px;
}

.top_section {
    display: flex;
    align-items: center;
    padding-bottom: 56px;
    justify-content: center;
}

.logo {
    font-size: 25px;
}

.bars {
    margin-left: 50px;
}

.menu-btn {
    border: none;
    background-color: transparent;
    outline: none;
    position: absolute;
    top: 16px;
    right: 0px;
}

.link {
    display: flex;
    align-items: center;
    color: white;
    text-decoration: none;
    padding: 10px 10px;
    gap: 15px;
    transition: all 0.5s ease-in-out;
    margin-bottom: 20px;
}

.link:hover {
    color: white;
    background: rgba(255, 255, 255, 0.25);
    border-radius: 5px;
}

.active {
    color: white;
    background: rgba(255, 255, 255, 0.25);
    border-radius: 5px;
}



.icon,
.link_text {
    text-decoration: none;
}

@media(max-width:768.99px) {
    .menu-btn {
        display: none;
    }

    .link {
        justify-content: center;
    }

    .sidebar-closed {
        width: 74px;
    }

}

@media(max-width:575.99px) {

    main {
        width: 90%;
    }
}