.dashboard-section {
    display: flex;
}

.dashboard-section .main-div {
    width: 100%;
}

.dashboard-section .main-div .header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: white;
    padding: 16px 60px;
}

.dashboard-section .main-div .header .header-heading {
    font-size: 32px;
    font-weight: 600;
    font-size: 32px;
    line-height: 24px;
}

.dashboard-content-div .dashboard-graphics {
    position: absolute;
    left: 0;
    right: 0;
    text-align: center;
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 36px;
    color: #212529;
    margin: 0 auto;
    margin-top: 40px;
    max-width: 360px;
}


.dashboard-content-div .dashboard-graphics img {
    width: 100%;
}

.bottom-btn-div {
    position: absolute;
    bottom: 48px;
    right: 60px;
    background: #0466D1;
    font-weight: 500;
    font-size: 20px;
    line-height: 36px;
    color: #FFFFFF;
    border-radius: 10px;
    padding: 12px 72px;
    border: none;
    outline: none;
}

.dashboard-section .main-div .dashboard-content-div {
    background-color: #F5F5F5;
    padding: 40px 60px 140px;
    position: relative;
}

@media(max-width:1600.99px) {
    .dashboard-section .main-div .dashboard-content-div {
        padding: 20px 20px 140px;
    }
}

@media(max-width:1366.99px) {
    .bottom-btn-div {
        padding: 10px 54px;
        right: 20px;
    }

    .dashboard-content-div .dashboard-graphics {
        font-size: 20px;
        line-height: 28px;
    }
}


@media(max-width:1024.99px) {

    .bottom-btn-div {
        padding: 6px 24px;
        font-size: 16px;
        line-height: 24px;
    }

    .dashboard-content-div .dashboard-graphics {
        display: none;
    }
}

@media(max-width:768.99px) {
    .dashboard-section .main-div .header .header-heading {
        font-size: 24px;
        line-height: 36px;
    }

    .dashboard-section .main-div .header {
        padding: 16px 40px;
    }
}

@media(max-width:414.99px) {
    .dashboard-section .main-div .header {
        padding: 16px 5px 16px 15px;
    }

    .dashboard-section .main-div .header .header-heading {
        font-size: 20px;
        line-height: 32px;
    }
}